import React from 'react'
import './Die.css'
import './Assets.css'

const Dice = [4, 6, 8, 10, 12];

class Die extends React.Component {
    constructor(props) {
        super(props);
  this.state = {
            count: 0,
            roll: 0,
        }
        this.handleClick = this.handleClick.bind(this);
        this.roll = this.roll.bind(this);
        this.increment = this.increment.bind(this);
        this.decrement = this.decrement.bind(this);
        this.clear = this.clear.bind(this);
        this.component = this.component.bind(this);
    }


    increment(e) {
        if (this.state.count >= 0  && this.state.count < 4) {
        this.setState({ count: this.state.count + 1 });
        this.setState({roll: 0})
        }
        else {
        }
    }

    decrement(e) {
        if (this.state.count > 0  && this.state.count < 5) {
        this.setState({ count: this.state.count - 1 });
        this.setState({roll: 0})
        }
        else {
        }
    }

    roll(e){
        this.setState({ roll: Math.floor(Math.random() * Dice[this.state.count] + 1)});
    }

    clear(e) {
        this.setState({ status: "Done"});
    }


    handleClick(e) {
        var die = document.getElementById("die");
        this.roll();
    }

    component(e) {
        var Component = 'D' + this.state.roll + 'D' + Dice[this.state.count];
    return Component

    }

    render() {
        
        return (
            <die className='noselect'>
            <forge>
            <div id='deforge' className='button' onClick={this.decrement} z-index='1'></div>
            <p id='level'>LEVEL {'D' + Dice[this.state.count]}</p>
            <div id='reforge' className='button' onClick={this.increment} z-index='1'></div>
            </forge>
            <div className='die-container'  onClick={ this.handleClick } z-index='0'>
            <div id='die' className={'d' + Dice[this.state.count] + '_' + this.state.roll}>
            </div>
            </div>
            
            </die>
        );
    }
}

export default Die;
